:root {
	--uanm-red: #d40000;
	--uanm-dark-purple: #383746;
	--uanm-light-purple: #75708f;
}

:root {
	--scheeles-green: #478800;
	--scheeles-blue: #00478A;
	--scheeles-purple: #8A0047;
	--mummy_brown: #8f4a21;
}

.test-1 {
	--green:#7abd80;
	--blue:#7F7ABD;
	--brown:#BD7F7A;
}
.test-2 {
	--green:#37ad65;
	--blue:#6537AE;
	--brown:#AE6537;
}
.test-3 {
	--green:#58b56f;
	--blue:#7058B6;
	--brown:#B67058;
}
.test-4 {
	--green:#5CE556;
	--blue:#575EE5;
	--brown:#E5575E;
}
.test-5 {
	--green:#39DF6F;
	--blue:#713ADF;
	--brown:#DF713A;
}



/** Put your custom @font-face font declarations in here ****************/
/** Typography ****************/
h1, h2, h3, h4, h5, h6 { font-weight:bold; } /* This helps to identify headings at the initial build stage, so I thought it'd be useful to have it */
.alert { background:red; color:white } /* 'alert' has a basic style, since it's useful to have it standing out for testing purposes.*/
select, input, textarea, button { font:99% sans-serif; }

body {
/* #444 looks better than black*/
    color: #444; font-size: 62.5%; /*allows for Em usage. 1em = 10px and so on*/
}
button, input[type="button"], input[type="reset"], input[type="submit"] { cursor: pointer; }
button[disabled], input[disabled] { cursor: default; }
*, *::after, *::before { -moz-box-sizing: border-box; -webkit-box-sizing: border-box; box-sizing: border-box; }
textarea { resize: vertical; }
/* These rather presentational classes are generated by WordPress, so it's useful to have some styles for them. Edit as you see fit. More info: http://codex.wordpress.org/CSS */
.alignnone { margin: 5px 20px 20px 0; }
.aligncenter, div.aligncenter { display: block; margin: 5px auto 5px auto; }
.alignright { float: right; margin: 5px 0 20px 20px; }
.alignleft { float: left; margin: 5px 20px 20px 0; }
a img.alignright { float: right; margin: 5px 0 20px 20px; }
a img.alignnone { margin: 5px 20px 20px 0; }
a img.alignleft { float: left; margin: 5px 20px 20px 0; }
a img.aligncenter { display: block; margin-left: auto; margin-right: auto }
.wp-caption { background: #fff; border: 1px solid #f0f0f0; max-width: 96%; padding: 5px 3px 10px; text-align: center; }
.wp-caption.alignnone { margin: 5px 20px 20px 0; }
.wp-caption.alignleft { margin: 5px 20px 20px 0; }
.wp-caption.alignright { margin: 5px 0 20px 20px; }
.wp-caption img { border: 0 none; height: auto; margin:0; max-width: 98.5%; padding:0; width: auto; }
.wp-caption p.wp-caption-text { font-size:11px; line-height:17px; margin:0; padding:0 4px 5px; }
br.clear { clear:both; display:block; height:1px; margin:-1px 0 0 0 } /* Use this br class to clear your floated columns */
/*Micro-Clearfix via http://nicolasgallagher.com/micro-clearfix-hack/ */
/* For modern browsers */
.cf:before, .cf:after { content:""; display:table; }
.cf:after { clear:both; }
/* For IE 6/7 (trigger hasLayout) */
.lte7 .cf { zoom:1; }
input:required {
	-moz-box-shadow:none;
	-webkit-box-shadow:none;
	-o-box-shadow:none;
	box-shadow:none;
}
html { font-size: 62.5%; }
body {
	font-size: 16px;
	font-size: 1.6rem;
	background-color: var(--uanm-dark-purple);
	color: #fff;
	font-family: Calibri, Candara, Segoe, Segoe UI, Optima, Arial, sans-serif;
	text-rendering: optimizeLegibility;
	-moz-osx-font-smoothing: grayscale;
} /* =16px */
button {
	font-family: Calibri, Candara, Segoe, Segoe UI, Optima, Arial, sans-serif;
}
a {
	color: #fff;
}

hr {
	color: var(--uanm-dark-purple);
}
#container {
	font-size: 1.6rem;
	margin: 0 auto;
	width: 100%;

	@media only screen and (min-width: 1224px) {
		width: 1224px;
	}
}

.is-type-video {
	height: auto;
	width: 100%;
}

.ays-poll-main {
	border: solid var(--uanm-red) 1px;
	background-color: var(--uanm-dark-purple);
	margin-left: auto;
	margin-right: auto;
}

.titledesc {
	h1, p {
		margin: 0;
	}
	p {
		font-size: 2rem;
		font-weight: bold;
	}
}

#colophon {
	background-color: var(--uanm-red);
	padding: 20px;
}

#branding {
	background-color: var(--uanm-light-purple);
	display: block;
	margin-bottom: 20px;
}

.top-wrap {
	align-items: center;
	display: flex;
	justify-content: space-between;
	padding: 15px;
	flex-flow: column;
}

.logo-title {
	display: flex;
	align-items: center;
}
.sitelogo {
	margin-right: 20px;
	height: 75px;
	width: 75px;
	svg {
		height: 75px;
		width: 75px;
	}
	&:hover {
		animation: rotation 2s infinite linear;
	}
	@media (prefers-reduced-motion) {
		&:hover {
			animation: none;
		}
	}
}

@keyframes rotation {
	from { transform: rotate(0deg); }
	to { transform: rotate(359deg); }
}

.archive-description,
.content article {
	background: var(--uanm-light-purple);
	margin-bottom: 20px;
	padding: 20px;
}

article.post .sponsor {
	border: solid var(--uanm-red) 1px;
	background-color: #222;
	padding: 10px;
	margin: 50px 0 25px;
}

#menu-social-menu,
#menu-social-menu-1 {
	padding-left: 0;
	display: flex;

	.menu-item {
		padding: 0 5px;
		&:last-child {
			padding-right: 0;
		}
		svg {
			width: 35px;
			height: 35px;
			fill: #fff;
		}
	}
	.social-icon-text{
		position: absolute;
		left: -9999px;
	}
}
#menu-social-menu {
	justify-content: space-between;
}
#menu-social-menu-1 {
	justify-content: space-around;
}

.mobile-nav-toggle {
	background-color: var(--uanm-red);
	border: 0;
	color: #fff;
	font-size: 2rem;
	font-weight: bold;
	padding: 15px;
	width: 100%;
}
#menu-primary {
	background-color: var(--uanm-red);
	font-size: 2.0rem;
	margin: 0;
	padding: 0;
	text-align: center;

	& > li {
		padding: 0;
	}

	a {
		display: block;
		font-weight: bold;
		padding: 15px;
		position: relative;
		text-decoration: none;
	}

	svg {
		fill: #fff;
		height: auto;
		width: 25px;
	}
}

.footer-widgets,
.wp-block-rss__item-title {
	a {
		position: relative;
		text-decoration: none;
	}
}

#menu-primary a::before,
.footer-widgets a::before,
.wp-block-rss__item-title a::before {
	content: "";
	position: absolute;
	display: block;
	width: 100%;
	height: 2px;
	bottom: 0;
	left: 0;
	background-color: #fff;
	transform: scaleX(0);
	transition: transform 0.3s ease;
}

#menu-primary a:hover::before,
.footer-widgets a:hover::before,
.wp-block-rss__item-title a:hover::before {
	transform: scaleX(1);
}

#searchform {
	align-items: center;
	display: flex;
	justify-content: center;
	margin: 20px 0;
}
#searchsubmit,
.primary-search,
.es_subscription_form_submit {
	border-radius: 5px;
	border: 0;
	padding: 6px;
}

.primary-search {
	margin: 0 10px;
	text-align: center;
	&:focus-visible {
		outline: 3px solid var(--uanm-red);
	}
}

header {
	h2 {
		font-size: 4.5rem;
		margin: 0 0 .50em;
		word-wrap: break-word;
	}
}

.post-meta {
	font-size: 1.6rem;
	font-weight: bold;
	margin: 1em 0;
}

.front-archive-wrap {
	display: flex;

	.frontpage-featured {
		flex: 1;
	}

	.frontpage-featured {
		&.poll {
			flex: 1;
		}
	}

	.e-content {
		flex: 4;
	}
	@media only screen and (min-width: 1024px) {
		.frontpage-featured,
		.frontpage-featured.poll {
			flex: 1;
			margin-right: 20px;
		}
	}
}

.frontpage-featured {
	img {
		height: auto;
	}
}

.e-content {
	h2, h3 {
		text-decoration: underline solid #383746 4px;
		text-underline-offset: 4px;
	}

	img {
		height: auto;
	}

	p {
		font-size: 2rem;
		line-height: 2.8rem;
		margin: 0 0 1.5em;
		&:last-of-type {
			margin-bottom: 0;
		}
	}

	li {
		font-size: 2rem;
	}

	blockquote {
		border-left: solid var(--uanm-dark-purple) 4px;
		padding-left: 10px;

		&.wp-block-quote p {
			margin-bottom: 1.5rem;
		}
	}

	figcaption {
		font-size: 1.6rem;
	}
}

h3.widget-title {
	text-decoration: underline;
	text-underline-offset: 4px;
}

.comment-content {
	p {
		font-size: 2rem;
		line-height: 2.8rem;
		margin: 2rem 0;
	}
}

.posts-nav,
.single-post-nav {
	display: flex;
	justify-content: space-between;
	margin: 20px 0;
	padding: 0 10px;
}

.cclicense img {
	vertical-align: text-top;
}

footer {
	.posts-nav,
	.single-post-nav {
		padding: 0;
	}
}

#comments {
	background-color: var(--uanm-light-purple);
	margin-bottom: 20px;
	padding: 20px;

	h3 {
		margin-top: 0;
	}

	li:not(:last-child) article.comment::after {
		content: '';
		border: solid var(--uanm-dark-purple) 3px;
		width: 15%;
		display: block;
		height: 0;
		margin: 20px 0;
	}
}

#comments-form {
	background-color: var(--uanm-light-purple);
	margin-bottom: 20px;
	padding: 20px;

	input[type="submit"] {
		border-radius: 5px;
		border: 0;
		padding: 6px;
	}
}

.comment-form {
	label {
		display: block;
		margin-bottom: 10px;
	}

	.comment-form-cookies-consent label {
		display: inline-block;
		width: 90%;
	}
	input[type="text"],
	input[type="email"],
	input[type="url"] {
		width: 100%;
	}
	input,
	textarea {
		border: 0;
		border-radius: 5px;
		height: 30px;
		padding: 6px;
		&:focus-visible {
			outline: 3px solid var(--uanm-red);
		}
	}

	input[type="submit"] {
		width: 100%;
	}

	textarea {
		height: 150px;
		width: 100%;
	}
}

.comment-meta {
	display: flex;
	align-items: center;
	flex-direction: column;
	justify-content: space-between;
}

.comment-meta-time {
	display: inline-block;
	width: 100%;
}

.separator {
	margin: 0 5px;
}
.comment-author {
	display: flex;
	align-items: center;
	width: 100%;

	img {
		margin-right: 15px;
		border-radius: 50%;
		border: 4px var(--uanm-dark-purple);
		border-style: dashed solid;
	}
}

.footer-widgets {
	display: flex;
	flex-flow: column;
	font-size: 1.9rem;

	li {
		line-height: 2.25rem;
		&.linkcat {
			line-height: normal;
		}
	}
	.blogroll {
		li {
			list-style-type: disc;
		}
	}
}
.movie-roll {
	font-size: 1.9rem;

	.wp-block-rss {
		display: flex;
		flex-wrap: wrap;
		padding-left: 0;
	}

	.wp-block-rss__item {
		flex: auto;
		list-style: none;
		text-align: center;
		width: 33%;
	}
}

.footer-widget {
	width: 100%;
}

#block-3 > ul,
.blogroll-block {
	list-style: none;
	padding-left: 0;
}
.encourage-comments {
	border: solid var(--uanm-dark-purple) 5px;
	border-left-width: 0;
	border-right-width: 0;
	margin-bottom: 20px;
}
.authorbio {
	align-items: center;
	border: solid var(--uanm-dark-purple) 5px;
	border-left-width: 0;
	border-right-width: 0;
	display: flex;
	margin: 0 auto 25px;
	padding: 10px 0;
	width: 100%;
}
.avatarwrap img {
	margin-right: 15px;
	max-width: none;
}

small {
	font-size: 100%;
}

#email-subscribers-form-1 {
	input[type="text"],
	input[type="email"] {
		width: 100%;
	}
}

.netflix-logo {
	background-color: rgb(0,0,0);
}

.post-type-archive-lists {
	.lists {
		text-align: center;
	}
}

.watched-flex,
.polls-trailers-interviews,
.home .movies {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
}
.watched-flex {
	article {
		width: 33%;
	}
}
.home .movies,
.polls-trailers-interviews {
	article {
		width: 100%;
	}
}
.watched-flex article,
.polls-trailers-interviews article.polls,
.home .movies {
	img {
		height: auto;
		max-width: 100%;
		width: 100%;
	}
}

.polls-trailers-interviews {
	article {
		text-align: center;
	}
	header {
		h3 {
			font-size: 3rem;
			margin-top: 0;
			text-align: left;
		}
	}
}

.home {
	h2 {
		text-align: center;
	}
}
.moremovieslink {
	display: flex;
	justify-content: end;
	padding: 0 15px;
}
@media only screen and (min-width: 600px) {
	.top-wrap {
		flex-flow: row;
	}

	#menu-social-menu {
		justify-content: flex-end;
	}

	article {
		&.polls {
			width: 25%;
		}
		&.interview {
			width: 50%;
		}
	}
}

@media only screen and (min-width: 768px) {

	.home {
		h2 {
			text-align: left;
		}

		.movies article {
			width: 20%;
		}
	}

	.front-archive-wrap .frontpage-featured {
		display: block;
	}

	#searchform {
		justify-content: end;
		margin: 0;
	}

	.mobile-nav-toggle {
		display: none;
	}

	#menu-primary {
		display: flex;
		justify-content: space-between;
		text-align: left;

		& > li {
			padding: 15px;
		}

		a {
			display: inline-block;
		}
	}

	.front-archive-wrap {
		.frontpage-featured,
		.frontpage-featured.poll {
			flex: 2;
		}
	}

	.comment-form {
		input[type="text"],
		input[type="email"],
		input[type="url"] {
			width: 30%;
		}
	}

	.comment-meta {
		flex-direction: row;
		justify-content: space-between;
	}

	.comment-author,
	.comment-meta-time {
		width: auto;
	}

	.footer-widget {
		width: 25%;
	}

	.is-type-video {
		margin: 0 auto;
		width: 50%;
	}
	.wp-block-column .is-type-video {
		width: 100%;
	}

	.footer-widgets {
		flex-flow: row;
	}

	.authorbio {
		width: 50%;
		margin-top: 25px;
		margin-bottom: 50px;
	}

	#list-grid {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		gap: 15px;
	}
	#list-grid {
		.content article {
			margin-bottom: 0;
		}
		article {
			width: 32%;
			flex: 1 1 auto;
			display: flex;
			flex-direction: column;
			justify-content: space-between;
		}
	}

	.page-id-6 ol {
		columns: 2;
		column-gap: 40px;
	}
}

@media only screen and (min-width: 900px) {
	#menu-primary {
		align-items: center;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
	}
}

@media only screen and (min-width: 1024px) {

	.home .polls-trailers-interviews article {
		text-align: center;
	}

	.comment-form input[type="submit"] {
		width: auto;
	}
}

